body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.active{
    font-weight: bold;
}
.active, .regular{
    cursor: pointer;
}
.folder-line{
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.folders{
    width: 90px;
    margin-top: 60px;
}
.unread-counter{
    font-weight: 700;
}
.hidden{
    display: none;
}
.active-letter {
    font-weight: 700;
}
.mail-list{
    width: 90%;
    margin-left: 100px;
}

.table-line{
    width: 90%;
    border: 1px solid #dddddd;
    min-height: 30px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.letter-wrapper{
    display: flex;
    align-items: center;
}
.letter-line{
    display: flex;
    align-items: center;
}
.no-letters{
    border: 1px solid #dddddd;
    width: calc(90% + 60px);
    min-height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #a8a8a8;
    margin-top: 55px;
}
.letter-name{
    cursor: pointer;
    overflow: hidden;
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px);
}
.name-blurred{
    color: #cccccc;
    -webkit-filter: blur(1px);
            filter: blur(1px);
}
.letter-text{
    background-color: #a8a8a8;
    -webkit-animation: paragraph .5s 1 ease-in-out;
            animation: paragraph .5s 1 ease-in-out;
    -webkit-transform-origin: right;
            transform-origin: right;
    font-weight: 200;
    height: auto;
    max-height: 30px;
    width: 70%;
    overflow: hidden;
    margin-right: 0;
    margin-left: auto;
    position: absolute;
    top: 0;
    right: 0;
    line-height: 30px;
    color: #fff;
}
@-webkit-keyframes paragraph {
    from {-webkit-transform: scaleX(0);transform: scaleX(0)}
    to {-webkit-transform: scaleY(1);transform: scaleY(1)}
}
@keyframes paragraph {
    from {-webkit-transform: scaleX(0);transform: scaleX(0)}
    to {-webkit-transform: scaleY(1);transform: scaleY(1)}
}
.back-icon {
    background-size: cover;
    width: 19px;
    height: 16px;
    display: inline-block;
    margin-right: 10px;
}
.icon-answered{
    background: url(/responsive-mail/static/media/recycle1.c6ef9768.svg) center center no-repeat;
}

.delete-letter-button{
    background: url(/responsive-mail/static/media/trashcan.1698fab7.svg) center center no-repeat;
    background-size: cover;
    width: 13px;
    height: 16px;
    margin-right: 10px;
    margin-left: 5px;
    cursor: pointer;
}
.delete-chosen{
    color: #eee;
    border: none;
    background-color: transparent;
    -webkit-transition: color .5s;
    transition: color .5s;
}
.delete-chosen:focus{
    outline: none;
}
.chosen-active{
    color: #a8a8a8;
    cursor: pointer;
}
.marked{
    background-color: #dddddd;
}
.main-region .letter-checkbox{
    display: block;
    -webkit-appearance: none;
    width: 14px;
    height: 14px;
    border: 1px solid #ddd;
    border-radius: 4px;
    position: relative;
    -webkit-transition: .2s;
    transition: .2s;
    margin-right: 10px;
}
.letter-checkbox:checked{
    border: 1px solid #a0a0a0;
}
.letter-checkbox:checked:before{
    content: '';
    width: 3px;
    height: 8px;
    top: 0;
    left: 4px;
    background-color: transparent;
    border: 1px solid #a0a0a0;
    position: absolute;
    border-left: none;
    border-top: none;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.group-checkbox{
    margin-left: 15px;
}
.date{
    font-weight: 200;
    font-size: 14px;
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px);
    margin-right: 10px;
    color: #A8A8A8;
}
@media screen and (max-width: 380px){
    .mail-list{
        width: 100%;
        margin-left: 0;
    }
    .table-line{
        width: 100%;
        border-left: none;
        border-right: none;
        border-top: none;
    }
    .letter-name{
        font-size: 14px;
    }
    .date{
        width: 80px;
    }
    .no-letters {
        border-left: none;
        border-right: none;
        width: 100%;
    }
}

.letter{
    /*margin: 30px auto;*/
    width: 40vw;
    height: auto;
}
.letter-inner{
    width: 38vw;
    height: 48vh;
    margin: auto;
}
.letter-title {
    height: 40px;
    display: flex;
    align-items: center;
}
.service-line{
    color: #a0a0a0;
    width: 60px;
}
.answer-letter{
    resize: none;
    width: 100%;
    height: auto;
    min-height: 100px;
    border: 1px solid #dddddd;
    margin-top: 10px;
}
.letter-title .back-icon {
    background-size: cover;
    width: 19px;
    height: 16px;
    display: inline-block;
    margin-right: 10px;
    margin-left: auto;
}
.icon-answered{
    background: url(/responsive-mail/static/media/recycle1.c6ef9768.svg) center center no-repeat;
}
.letter-buttons{
    border-radius: 5px;
    border: none;
    width: 15%;
    height: 7%;
}
.letter-buttons:focus{
    outline: none;
}
.ignore-button{
    margin-left: 100px;
    background-color: transparent;
    border: 1px solid #a0a0a0;
    color: #a0a0a0;
}
.send-button{
    background-color: #a0a0a0;
    color: #fff;
}
.ignore-only{
    margin-left: 0;
}

@media screen and (max-width: 380px){
    .letter{
       width: 100%;
    }
    .letter-inner{
        width: 98%;
        height: auto;
    }
    .answer-letter {
        width: 98%;
    }
    .incoming-letter{
        height: 30vh;
        overflow: scroll;
    }
}
.drawer-box{
    width: 50vw;
    height: 100vh;
    background-color: #fff;
    box-shadow: 1px 0 7px rgba(0, 0, 0, 0.5);
    position: absolute;
    z-index: 50;
}
.drawer-area{
    height: calc(100vh - 30px);
    overflow: hidden;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
.drawer-box-header{
    height: 30px;
    background-color: #1f1f1f;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.drawer-box-close{
    color: #fff;
    margin-right: 10px;
    margin-top: -5px;
    cursor: pointer;
}
@media screen and (max-width: 380px){
    .drawer-box {
        width: 100vw;
    }
}

.new-letter{
    width: 400px;
    /*height: 300px;*/
    background-color: #fff;
    /*border: 1px solid red;*/
    position: absolute;
    z-index: 100;
    /*top: 15vh;*/
    /*left: 15vw;*/
}
.new-letter-header{
    background-color: #1f1f1f;
    height: 30px;
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.new-letter-header-message{
    margin-left: 10px;
}
.new-letter-close{
    margin-right: 10px;
    cursor: pointer;
}
.new-letter-form{
    display: flex;
    flex-direction: column;
}
.new-letter-form button{
    width: 30%;
    margin-top: 25px;
    margin-left: 10px;
    border-radius: 3px;
}
.new-letter-form label{
    display: flex;
    align-items: flex-end;
    margin-left: 10px;
}
.new-letter-form input, input:focus{
    display: inline-block;
    font-size: 16px;
    padding: 0;
    width: 310px;
    height: 25px;
    border: none;
    outline: none;
    border-bottom: 1px solid #ddd;
}
.new-letter-form textarea{
    margin-top: 25px;
    resize: none;
    background-color: #fafafa;
}
.new-letter-form textarea:focus{
    outline: none;
}
.new-letter-form-align{
    width: 70px;
    display: inline-block;
}
.search-input, .search-input:focus{
    width: 30%;
    height: 30px;
    margin-left: 160px;
    margin-top: -30px;
    border: 1px solid #ddd;
    font-size: 16px;
    line-height: 1.6rem;
    padding: 0;
}

.search-input::-webkit-input-placeholder{
    color: #ddd;
    padding-left: 5px;
}

.search-input::-ms-input-placeholder{
    color: #ddd;
    padding-left: 5px;
}

.search-input::placeholder{
    color: #ddd;
    padding-left: 5px;
}
.hamburger-button{
    width: 30px;
    height: 30px;
    cursor: pointer;
    position: absolute;
    z-index: 200;
    top: 5px;
    left: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.hamburger{
    background-color: #fff;
    width: 30px;
    height: 3px;
    position: absolute;
    z-index: 100;
}
.first-start{
    -webkit-transform: translateY(-8px);
            transform: translateY(-8px);
}
.third-start{
    -webkit-transform: translateY(8px);
            transform: translateY(8px);

}
.first-in{
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-animation: first-in .8s cubic-bezier(.56,1.7,.34,.89) forwards;
            animation: first-in .8s cubic-bezier(.56,1.7,.34,.89) forwards;
}

.third-in{
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-animation: third-in .8s cubic-bezier(.56,1.7,.34,.89) forwards;
            animation: third-in .8s cubic-bezier(.56,1.7,.34,.89) forwards;
}

.second-in{
    opacity: 0;
}
.second-out{
    -webkit-animation: second-out .8s forwards;
            animation: second-out .8s forwards;
}
.first-out{
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-animation: first-out .8s ease forwards;
            animation: first-out .8s ease forwards;
}
.third-out{
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-animation: third-out .8s ease forwards;
            animation: third-out .8s ease forwards;
}
@-webkit-keyframes first-in {
    from {-webkit-transform: translateY(-8px);transform: translateY(-8px)}
    25% {-webkit-transform: translateY(0);transform: translateY(0)}
    to {-webkit-transform: translateY(0) rotate(-135deg);transform: translateY(0) rotate(-135deg)}
}
@keyframes first-in {
    from {-webkit-transform: translateY(-8px);transform: translateY(-8px)}
    25% {-webkit-transform: translateY(0);transform: translateY(0)}
    to {-webkit-transform: translateY(0) rotate(-135deg);transform: translateY(0) rotate(-135deg)}
}

@-webkit-keyframes third-in {
    from {-webkit-transform: translateY(8px);transform: translateY(8px)}
    25% {-webkit-transform: translateY(0);transform: translateY(0)}
    to {-webkit-transform: translateY(0) rotate(135deg);transform: translateY(0) rotate(135deg)}
}

@keyframes third-in {
    from {-webkit-transform: translateY(8px);transform: translateY(8px)}
    25% {-webkit-transform: translateY(0);transform: translateY(0)}
    to {-webkit-transform: translateY(0) rotate(135deg);transform: translateY(0) rotate(135deg)}
}
@-webkit-keyframes first-out {
    25%, 100% {
        -webkit-animation-timing-function: cubic-bezier(.56,1.7,.34,.89);
                animation-timing-function: cubic-bezier(.56,1.7,.34,.89);
    }
    from {-webkit-transform: rotate(-135deg) translateY(0);transform: rotate(-135deg) translateY(0)}
    25% {-webkit-transform: rotate(0deg) translateY(0);transform: rotate(0deg) translateY(0)}
    to {-webkit-transform: rotate(0deg) translateY(-8px);transform: rotate(0deg) translateY(-8px)}
}
@keyframes first-out {
    25%, 100% {
        -webkit-animation-timing-function: cubic-bezier(.56,1.7,.34,.89);
                animation-timing-function: cubic-bezier(.56,1.7,.34,.89);
    }
    from {-webkit-transform: rotate(-135deg) translateY(0);transform: rotate(-135deg) translateY(0)}
    25% {-webkit-transform: rotate(0deg) translateY(0);transform: rotate(0deg) translateY(0)}
    to {-webkit-transform: rotate(0deg) translateY(-8px);transform: rotate(0deg) translateY(-8px)}
}

@-webkit-keyframes third-out {
    25%, 100% {
        -webkit-animation-timing-function: cubic-bezier(.56,1.7,.34,.89);
                animation-timing-function: cubic-bezier(.56,1.7,.34,.89);
    }
    from {-webkit-transform: rotate(135deg) translateY(0);transform: rotate(135deg) translateY(0)}
    25% {-webkit-transform: rotate(0deg) translateY(0);transform: rotate(0deg) translateY(0)}
    to {-webkit-transform: rotate(0deg) translateY(8px);transform: rotate(0deg) translateY(8px)}
}

@keyframes third-out {
    25%, 100% {
        -webkit-animation-timing-function: cubic-bezier(.56,1.7,.34,.89);
                animation-timing-function: cubic-bezier(.56,1.7,.34,.89);
    }
    from {-webkit-transform: rotate(135deg) translateY(0);transform: rotate(135deg) translateY(0)}
    25% {-webkit-transform: rotate(0deg) translateY(0);transform: rotate(0deg) translateY(0)}
    to {-webkit-transform: rotate(0deg) translateY(8px);transform: rotate(0deg) translateY(8px)}
}
@-webkit-keyframes second-out {
    0% {opacity: 0}
    49% {opacity: 0}
    50%, 100% {opacity: 1}
}
@keyframes second-out {
    0% {opacity: 0}
    49% {opacity: 0}
    50%, 100% {opacity: 1}
}











